body {
  padding: 0;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: white;

  box-sizing: border-box;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

.button {
  width: 280px;
  height: 40px;
  color: white;
  background-color:$color-main;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
}
